import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  ColumnsProps,
  DefaultSortProps,
  enumColumn,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { CLASS_NAME, SaldoAnteriorPlanoConta } from '../../../../models/dtos/mcg/saldoAnteriorPlanoConta';
import { NATUREZA_CREDOR_DEVEDOR_ENUM_VALUES } from '../../../../models/enumerated/mcg/naturezaCredorDevedorEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'planoConta.conta',
    order: OrderType.ASC,
  },
];

const SaldoAnteriorPlanoContaSearchView: FC<SearchViewProps<SaldoAnteriorPlanoConta>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const empresa = globalReducer.globalParameter.empresa;

  const columns: ColumnsProps[] = [
    {
      dataField: 'planoConta.conta',
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Conta',
    },
    {
      dataField: 'planoConta.codigoReduzido',
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Codigo Reduzido',
    },
    {
      dataField: 'planoConta.descricao',
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    {
      dataField: 'valor',
      filter: {
        type: FilterType.NUMBER,
      },
      formatter: (cell, row) => {
        return row.valor > 0 ? row.valor : -row.valor;
      },
      sort: true,
      text: 'Valor',
    },
    enumColumn(
      {
        dataField: 'naturezaCredorDevedor',
        filter: {
          type: FilterType.SELECT,
        },
        sort: true,
        text: 'Natureza',
      },
      NATUREZA_CREDOR_DEVEDOR_ENUM_VALUES
    ),
  ];

  function getSearchExpression(): SearchExpression[] {
    return [
      {
        fieldName: 'empresa.id',
        value: empresa.id,
      },
    ];
  }

  return (
    <WindowComponent
      title="Manutenção de Saldo Anterior - Plano Conta"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter)]}
      wikiId={2227}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'SaldoAnteriorPlanoContaEditView', CLASS_NAME)}
        searchExpressions={getSearchExpression}
      />
    </WindowComponent>
  );
};

export default SaldoAnteriorPlanoContaSearchView;
